import React, { useState, useEffect, createRef } from "react";
import Header from "./Header";
import Mics from "./Mics";
import { Box, Container, Hidden } from "@mui/material";
import axios from "axios";
import ReactPlayer from "react-player";
import AudioPlayer from "./AudiPlayer/AudioPlayer";
import Sound_Cloud from "./Sound_Cloud";

//randomizing or shuffling
function shuffleFisherYates(array) {
  let i = array.length;
  while (i--) {
    const ri = Math.floor(Math.random() * i);
    [array[i], array[ri]] = [array[ri], array[i]];
  }
  return array;
}

const Layout = ({ children }) => {
  const [soundCloudAudio, setSoundCloudAudio] = useState({
    audio: null,
    trackID: null,
  });

  const [songs, setSongs] = useState([]);

  /// Fetch Playlist from API
  const getAudioList = () => {
    axios
      .get("/api/radioList")
      .then((response) => {
        console.log(response)
        //setSongs(response.data)
        setSongs(shuffleFisherYates(response.data));
      })
      .catch(function (error) {
        console.log(error);
      });
  };
  // To Fetch and Start the Playlist
  useEffect(() => {
    getAudioList();
  }, []);

  return (
    <>
      <Container maxWidth="md">
        <Header />
        <Mics />
        <Box sx={{ width: "100%" }}>
          <div style={{ display: soundCloudAudio.trackID ? "none" : "block" }}>
            {songs?.length && <AudioPlayer songs={songs} />}
          </div>
          <div
            style={{ display: soundCloudAudio.trackID ? "block" : "none" }}
          ></div>
        </Box>
        {children}
      </Container>
    </>
  );
};

export default Layout;
